body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App-header {
  display: flex;
  justify-content: center;
  font-family: 'Cabin Sketch', cursive;
  font-size: 18px;
}

.App-main {
  display: flex; 
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.App-footer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.box {
  width: 15rem;
  height: 15rem;
  border: 1px solid black;
  margin: 1rem;
  display: flex; 
  justify-content: center;
  align-items: center;
  border-style: solid;
  border-width: medium;
  border-color: black; 
  font-family: 'Copse', serif;

}

.buttons-row {
  display: flex;
  justify-content: space-around;
  width: 10rem;
}

.btn {
  width: 3rem;
  height: 3rem;
  border-radius: 50%;
  border-style: solid;
  border-width: medium;
  border-color: black;
  display: flex;
  justify-content: center;
  font-family: 'Copse', serif;

}
